    a.has-popup {
        position: relative;
        .popup {
            margin: 0px;
            cursor: text;
            &.hidden {
                transform: translate(-50%, -10px) scale(0.5);
                pointer-events: none;
                opacity: 0; }
            display: block;
            text-align: center;
            font-family: "Rubik", sans-serif;
            position: absolute;
            transition: $transition-20 all;
            transform: translate(-50%, 0px);
            font-size: 14px;
            border: none;
            outline: none;
            color: $darkest;
            top: 100%;
            left: 50%;
            padding: 5px;
            background-color: transparent; } } }
.logo {
    margin: 50px;
    width: 150px;
    height: 150px; }
section {
    width: 100%;
    margin-bottom: 50px;
    transition: $transition-40 all;
    overflow: hidden;
    &.hidden {
        opacity: 0; }
    &.removed {
        display: none; }
    .header {
        width: 100%;
        p {
            margin: auto;
            text-align: center; }
        p.title {
            font-size: 26px;
            margin-bottom: 4px; }
        p.description {} }
    .thumbnails, .cards {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        .item {
            border-radius: 10px;
            width: 200px;
            margin: 40px;
            padding: 0px;
            overflow: hidden;
            transition: $transition-30 transform;
            &:hover {
                transform: scale(1.1);
                img {
                    transform: scale(1.3); } }
            &.hidden {
                display: none; }
            img {
                transition: $transition-100 all;
                width: 100%;
                display: block; } } }
    &.video, &.synctan {
        .item {
            display: flex;
            align-items: center;
            height: calc(200px/16*9);
            img {
                transform: scale(1.02); } } }
    &.soundcloud {
        .item {
            width: 180px;
            margin: 40px 50px;
            height: auto; } }
    &.github {
        .cards {
            margin-top: 10px; }
        .item {
            display: flex;
            flex-direction: column;
            width: calc(200px + 20px);
            margin: calc(50px - 20px);
            border: 2px solid #ececec;
            box-sizing: border-box;
            backface-visibility: hidden;
            &:hover {
                transform: none; }
            p.title {
                margin: 18px;
                margin-bottom: 10px;
                font-size: 18px; }
            p.description {
                margin: 0px 18px;
                font-size: 14px;
                color: #5a5a5a; }
            .urls {
                display: flex;
                justify-content: flex-end;
                margin-top: auto;
                a.repo-url, a.repo-website {
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: 22px;
                    padding: 10px;
                    cursor: pointer;
                    display: block;
                    &.hidden {
                        display: none; } } }
            .repo-url {
                height: 26px;
                width: 26px;
                background-image: url('data:image/svg+xml;utf8,<svg fill="%233f3f3f" version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path d="M16 0.395c-8.836 0-16 7.163-16 16 0 7.069 4.585 13.067 10.942 15.182 0.8 0.148 1.094-0.347 1.094-0.77 0-0.381-0.015-1.642-0.022-2.979-4.452 0.968-5.391-1.888-5.391-1.888-0.728-1.849-1.776-2.341-1.776-2.341-1.452-0.993 0.11-0.973 0.11-0.973 1.606 0.113 2.452 1.649 2.452 1.649 1.427 2.446 3.743 1.739 4.656 1.33 0.143-1.034 0.558-1.74 1.016-2.14-3.554-0.404-7.29-1.777-7.29-7.907 0-1.747 0.625-3.174 1.649-4.295-0.166-0.403-0.714-2.030 0.155-4.234 0 0 1.344-0.43 4.401 1.64 1.276-0.355 2.645-0.532 4.005-0.539 1.359 0.006 2.729 0.184 4.008 0.539 3.054-2.070 4.395-1.64 4.395-1.64 0.871 2.204 0.323 3.831 0.157 4.234 1.026 1.12 1.647 2.548 1.647 4.295 0 6.145-3.743 7.498-7.306 7.895 0.574 0.497 1.085 1.47 1.085 2.963 0 2.141-0.019 3.864-0.019 4.391 0 0.426 0.288 0.925 1.099 0.768 6.354-2.118 10.933-8.113 10.933-15.18 0-8.837-7.164-16-16-16z"></path></svg>'); }
            .repo-website {
                height: 26px;
